<template>
  <div>
    <router-link to="/">Home</router-link>
    <template v-if="search">
        <input type="text" @keyup.enter="SetSearch" ref="searchtext" :value="stext" />
        <button @click="SetSearch">검색</button>
    </template>
    
    <div v-if="login">
        <strong>{{userid}}</strong>님 환영합니다.
        <a href="#" @click.prevent="moveInfo">회원정보</a>
        <a href="#" @click.prevent="logout">로그아웃</a>
    </div>
    <div v-else>
        <a href="#" @click.prevent="loginmodal">로그인</a>
        <a href="#" @click.prevent="moveInfo">(회원정보-테스트)</a>
    </div>
  </div>
</template>

<script>
import {mapState,mapGetters,mapActions} from 'vuex';

export default {
    props:['search'],
    computed:{
        ...mapState({
            searchtext: state => state.search.stext
        }),
        ...mapGetters('search',{stext: 'stext'}),
        ...mapGetters('user',{login: 'login', userid: 'userid'}),
    },
    methods:{
        ...mapActions('search',['update_stext']),
        SetSearch(){
            this.update_stext(this.$refs.searchtext.value);
            alert("set");
            //this.$store.dispatch('update_stext',this.$refs.searchtext.value);
        },
        moveInfo(){
            this.$router.push('/user/info');
        },
        loginmodal(){
            alert("로그인 모달");
            //로그인 모달 띄우기
        },
        logout(){
            alert("로그아웃");
            //로그아웃
        }
    }
}
</script>